	module.exports = {"data":{"markdownRemark":{"html":"<p>It's ALIVE! Just click Dr Frankenstein over there to experience the majesty of my Pomodoro Timer. <a href=\"http://pomodoro-timer-rs.herokuapp.com/\" rel=\"ITS ALIVE (Pomodoro app link)\" target=\"_blank\"><img src=\"http://graphiccon.com/wp-content/uploads/2016/01/its-alive-1200x475.jpg\" alt=\"Gene Wilder as Frankenstein\"></a></p>\n<h1><a href=\"http://pomodoro-timer-rs.herokuapp.com/\">Pomodoro Timer</a></h1>\n<p>A Pomodoro Timer is a timer that allows for following the Pomodoro Technique, which is 25 minutes of focus followed by 5 minutes of rest (or really, unfocused thinking, day dreaming, type activities). This allows the brain-mortar to dry when you are building new neurological connections.</p>\n<h2>My Pomodoro App</h2>\n<p>Built in AngularJS, it provides the basic timer for 25 min work and 5 min relaxation, plus every fourth round the rest period extends to 30 minutes. It also connects to Firebase for tracking activities.</p>\n<h2>Roadmap</h2>\n<p>There are a number of things I hope to continue to add.</p>\n<ol>\n<li>Giant rotating tomato - this proved a time suck because I need to photograph a rotating tomato - CSS animations and JS animations both do better on a 'axis-on' view (think of looking at a spinning tire hubcap) instead of 'equator' view (think tire tread). I want to show timing hash marks so would need to take a number of pictures of the tread and then animate a transition between pictures, estimating that for 360 degrees and 30 minutes I would need 30 images representing 12 degrees each. Each minute I am transitioning from minute to minute.</li>\n<li>Login - right now anyone can add a task specific to everyone, which is not really a great outcome. I am running on security by obscurity and blogging about it so that is not all that bright. Ideally I would create a login and signup mechanism so everyone could use their own task list.</li>\n<li>Task specific tracking - I would want to know how many times per task I am interrupted (have to hit the pause button) and how many task-break cycles I have completed. It would be great to be able to select and existing or create new task when I hit the start button.</li>\n</ol>","frontmatter":{"date":"May 06, 2016","path":"/blog/pomodoro-timer/","title":"Pomodoro Timer","type":"blog"}}},"pathContext":{}}

